body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }

  .blob {
    background: #ffffff;
    border-radius: 50%;
    height: 64px;
    width: 64px;
    position: absolute;
    transition: all 1s ease-out 0s;
    top:224px;
    left:224px;
  }
  
  .blob.blue {
    background: #456BD9;
  }

  .blob.blue.highlight {
    background: #5b7bd8;
  }

  .blob.red {
    background: #d94545;
  }

  .blob.red.highlight {
    background: #db5656;
  }

  .blob.destroy {
    background: #ffffff;
  }

  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 64px;
    height: 64px;
    text-align: center;
    width: 64px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: column; 
    position: absolute; 
    left: 50%;
    transform: translate(-50%, 0);
    /*left: 0; 
    right: 0; 
    margin-left: auto; 
    margin-right: auto; 
    width: 400px; Need a specific value to work */
  }
  
  .game-info {
    margin: 10 auto;
  }
  
